import { Component, Input, HostListener, ViewChild, Output, EventEmitter, ElementRef } from '@angular/core';
import { PopoverComponent } from '@common/popover/popover.component';

@Component({
    selector: 'app-guarded-action',
    templateUrl: './guarded-action.component.html',
    styleUrls: ['./guarded-action.component.scss'],
})
export class GuardedActionComponent {
    @Input()
    public question: string = '';

    @Input()
    public additional: Array<string> = [];

    @Input()
    public action: string = '';

    @Input()
    public align: string = 'left';

    @Output()
    public confirmed = new EventEmitter<void>();

    @ViewChild('popover', { static: true })
    public popover: PopoverComponent;

    @ViewChild('actionButton', { static: true })
    public actionButton: ElementRef;

    constructor(private host: ElementRef) { }

    @HostListener('click', ['$event'])
    public onClick($event: MouseEvent) {
        this.popover.target = this.host.nativeElement;
        this.popover.toggle($event, true);
        setTimeout(() => this.actionButton.nativeElement.focus());
    }
}
