import { Component, Input, OnInit, forwardRef, OnChanges, TemplateRef, ViewChild, ElementRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

export interface SelectionBoxItem {
    display: string;
    header?: string;
    value: any;
    data: any;
}

@Component({
    selector: 'app-selection-box',
    templateUrl: './selection-box.component.html',
    styleUrls: ['./selection-box.component.scss'],
    providers: [{
        provide: NG_VALUE_ACCESSOR,
        useExisting: forwardRef(() => SelectionBoxComponent),
        multi: true
    }]
})
export class SelectionBoxComponent implements ControlValueAccessor, OnInit, OnChanges {
    @Input()
    public items: Array<SelectionBoxItem> = [];

    @Input()
    public max: number = 0;

    @Input()
    public placeholder: string = 'Search...';

    @Input()
    public itemTemplate: TemplateRef<any>;

    @ViewChild('input', { static: true })
    public input: ElementRef;

    public search: string = '';
    public filtered: Array<SelectionBoxItem> = [];
    public focused: boolean = false;
    public focusedItem: SelectionBoxItem;
    public selectedItems: Array<SelectionBoxItem> = [];

    private values: Array<any> = [];
    public writeValue(value: any) {
        this.values = value || [];
        this.updateFilteredItems();
    }

    private onChange: any;
    public registerOnChange(value: any) { this.onChange = value; }
    private onTouched: any;
    public registerOnTouched(value: any) { this.onTouched = value; }

    public ngOnInit() { this.updateFilteredItems(); }
    public ngOnChanges() { this.updateFilteredItems(); }

    public onFocusChange(value: boolean) {
        if (value) {
            this.focused = true;
        } else {
            setTimeout(() => {
                this.focused = false;
                this.focusedItem = undefined;
                this.search = '';
                this.updateFilteredItems();
            }, 150);
        }
    }

    public updateFilteredItems() {
        this.selectedItems = this.values.map(o => this.items.find(i => i.value === o)).filter(o => o);

        const search = (this.search || '').trim().toLowerCase();
        this.filtered = this.items.filter(o => {
            return this.selectedItems.indexOf(o) < 0 &&
                (!search || o.display.toLowerCase().indexOf(search) >= 0);
        });
    }

    public onArrowDown() {
        let index = -1;
        if (this.focusedItem) { index = this.filtered.indexOf(this.focusedItem); }

        if (index >= 0 && index < this.filtered.length - 1) {
            this.focusedItem = this.filtered[index + 1];
        } else {
            this.focusedItem = this.filtered.length ? this.filtered[0] : undefined;
        }
    }

    public onArrowUp() {
        let index = -1;
        if (this.focusedItem) { index = this.filtered.indexOf(this.focusedItem); }

        if (index > 0 && index < this.filtered.length) {
            this.focusedItem = this.filtered[index - 1];
        } else {
            this.focusedItem = this.filtered.length ? this.filtered[this.filtered.length - 1] : undefined;
        }
    }

    public onSelect(item: SelectionBoxItem) {
        if (this.filtered.indexOf(item) < 0) { return; }

        this.selectedItems.push(item);
        this.values = this.selectedItems.map(o => o.value);
        this.onChange(this.values);

        this.input.nativeElement.blur();
    }

    public onRemove(item: SelectionBoxItem) {
        this.selectedItems = this.selectedItems.filter(o => o !== item);
        this.values = this.selectedItems.map(o => o.value);
        this.onChange(this.values);

        this.updateFilteredItems();
    }
}
