import { Component, OnInit } from "@angular/core";
import { AppService } from '@services/app/app.service';
import { App } from '@services/app/app.api';
import { ActivatedRoute, Router } from '@angular/router';
import { finalize, mergeMap, tap } from 'rxjs/operators';
import { Utility } from '@classes/utility';
import { FormGroup, FormControl } from '@angular/forms';
import { NotificationsService } from 'angular2-notifications';
import { forkJoin, of } from 'rxjs';

@Component({
    selector: 'app-speaker-detail',
    templateUrl: './speaker-detail.component.html',
    styleUrls: ['./speaker-detail.component.scss']
})
export class SpeakerDetailComponent implements OnInit {
    public Utility = Utility;

    public loading = true;
    public speaker: App.Speaker;
    public media: Array<App.Media> = [];
    public form: FormGroup;
    public imageUrl: string;

    constructor(
        private app: AppService,
        private route: ActivatedRoute,
        private router: Router,
        private notifications: NotificationsService) { }

    public ngOnInit() {
        this.form = new FormGroup({});
        this.form.addControl('name', new FormControl(undefined));
        this.form.addControl('description', new FormControl(undefined));

        let id = parseInt(this.route.snapshot.params.id);
        forkJoin(this.app.API.getSpeaker(id), this.app.API.getAllMedia()).pipe(finalize(() => {
            this.loading = false;
        })).subscribe(responses => {
            this.speaker = responses[0];
            this.media = responses[1].filter(o => (o.speakerIds || []).indexOf(this.speaker.id) >= 0);
            this.form.controls.name.setValue(this.speaker.name);
            this.form.controls.description.setValue(this.speaker.description);
            this.imageUrl = this.speaker.imageUrl ? `${this.speaker.imageUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
        });
    }

    public saving: boolean = false;
    public onSave() {
        let speaker: App.SpeakerUpdate = { id: this.speaker.id };
        speaker.name = this.form.controls.name.value;
        speaker.description = this.form.controls.description.value || null;

        this.saving = true;
        this.app.API.updateSpeaker(speaker).pipe(finalize(() => {
            this.saving = false;
        })).subscribe(speaker => {
            this.speaker = speaker;
            this.notifications.success('Success', 'Speaker details saved successfully.', { timeOut: 5000 });
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem saving.');
        });
    }

    public uploading: File;
    public progress: number;

    public onImageSelected(event: any) {
        let file: File = event.target?.files?.length && event.target.files[0] || undefined;
        if (!file) { return; }

        this.uploading = file;
        this.progress = 0;

        this.app.API.uploadImageForSpeaker(this.speaker, file, progress => {
            this.progress = progress;
        }).pipe(finalize(() => {
            this.uploading = undefined;
            this.progress = 1;
        })).subscribe(speaker => {
            this.notifications.success('Success', 'Speaker image updated.', { timeOut: 5000 });
            this.speaker = speaker;
            this.imageUrl = this.speaker.imageUrl ? `${this.speaker.imageUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem uploading.');
        });
    }

    public deleteDialogVisible: boolean = false;
    public deleting: boolean = false;

    public onDelete() {
        this.deleting = true;
        this.app.API.deleteSpeaker(this.speaker).pipe(finalize(() => {
            this.deleting = false;
        })).subscribe(() => {
            this.router.navigate(['/speakers']);
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem deleting.');
        });
    }
}
