import { Observable } from 'rxjs';
import { Manager } from '@classes/manager';
import { App, AppAPI } from '../app.api';

export class SpeakerManager extends Manager {
    public static sharedInstance: SpeakerManager = undefined;

    public speakers: Array<App.Speaker> = [];

    constructor(private api: AppAPI) {
        super();
        SpeakerManager.sharedInstance = this;
    }

    protected fetchData(): Observable<any> {
        return this.api.getAllSpeakers();
    }

    protected setData(data: any) {
        this.speakers = data;
    }

    public reset() {
        super.reset();
        this.speakers = [];
    }
}
