import { Component, OnInit } from "@angular/core";
import { AppService } from '@services/app/app.service';
import { App } from '@services/app/app.api';
import { ActivatedRoute } from '@angular/router';
import { finalize, mergeMap, map, tap } from 'rxjs/operators';
import { FormGroup, FormControl } from '@angular/forms';
import { NotificationsService } from 'angular2-notifications';
import { OrganizationManager } from '@services/app/managers/organization.manager';
import { of } from 'rxjs';

@Component({
    selector: 'app-organization-detail',
    templateUrl: './organization-detail.component.html',
    styleUrls: ['./organization-detail.component.scss']
})
export class OrganizationDetailComponent implements OnInit {
    public organization: App.Organization;
    public form: FormGroup;
    public imageUrl: string;

    constructor(private app: AppService, private route: ActivatedRoute, private notifications: NotificationsService) { }

    public ngOnInit() {
        this.organization = OrganizationManager.sharedInstance.organization;

        this.form = new FormGroup({});
        this.form.addControl('name', new FormControl(this.organization.name));
        this.form.addControl('description', new FormControl(this.organization.description));
        this.form.addControl('url', new FormControl(this.organization.url));

        this.imageUrl = this.organization.imageUrl ? `${this.organization.imageUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
    }

    public saving: boolean = false;
    public onSave() {
        let organization: App.OrganizationUpdate = { id: this.organization.id };
        organization.name = this.form.controls.name.value;
        organization.url = this.form.controls.url.value;
        organization.description = this.form.controls.description.value || null;

        this.saving = true;
        this.app.API.updateOrganization(organization).pipe(finalize(() => {
            this.saving = false;
        })).subscribe(organization => {
            this.organization = organization;
            this.notifications.success('Success', 'Organization details saved successfully.', { timeOut: 5000 });
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem saving.');
        });
    }

    public uploading: File;
    public progress: number;

    public onImageSelected(event: any) {
        let file: File = event.target?.files?.length && event.target.files[0] || undefined;
        if (!file) { return; }

        this.uploading = file;
        this.progress = 0;

        this.app.API.uploadImageForOrganization(this.organization, file, progress => {
            this.progress = progress;
        }).pipe(finalize(() => {
            this.uploading = undefined;
            this.progress = 1;
        })).subscribe(organization => {
            this.notifications.success('Success', 'Organization image updated.', { timeOut: 5000 });
            this.organization = this.app.organizationManager.organization = organization;
            this.imageUrl = this.organization.imageUrl ? `${this.organization.imageUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem uploading.');
        });
    }
}
