import { Component, LOCALE_ID, Inject, Input, OnChanges } from "@angular/core";
import { App } from '@services/app/app.api';
import { formatDate } from '@angular/common';
import { Utility } from '@classes/utility';
import { SeriesManager } from '@services/app/managers/series.manager';
import { SpeakerManager } from '@services/app/managers/speaker.manager';

interface MediaListEntry {
    id: number;
    date: Date;
    dateString: string;
    timeString: string;
    series: string;
    name: string;
    fullName: string;
    speakers: Array<string>;
    status: number;
    statusString: string;
    statusColor: string;
    search?: string;
}

@Component({
    selector: 'app-reusable-media-list',
    templateUrl: './reusable-media-list.component.html',
    styleUrls: ['./reusable-media-list.component.scss']
})
export class ReusableMediaListComponent implements OnChanges {
    public Utility = Utility;

    @Input()
    public media: Array<App.Media>;

    private seriesById: { [key: number]: App.Series } = {};
    private speakerById: { [key: number]: App.Speaker } = {};

    public entries: Array<MediaListEntry> = [];
    public filtered: Array<MediaListEntry> = [];
    public loaded: Array<MediaListEntry> = [];
    public sort: string = 'id';
    public ascending: boolean = false;
    public search: string;

    constructor(@Inject(LOCALE_ID) public locale: string) {
        SeriesManager.sharedInstance.series.forEach(o => this.seriesById[o.id] = o);
        SpeakerManager.sharedInstance.speakers.forEach(o => this.speakerById[o.id] = o);
    }

    public ngOnChanges() {
        this.entries = (this.media || []).map(o => {
            const series: string = o.seriesId && this.seriesById[o.seriesId] ? this.seriesById[o.seriesId].name : undefined;

            let result: MediaListEntry = {
                id: o.id,
                date: o.date,
                dateString: formatDate(o.date, 'mediumDate', this.locale),
                timeString: formatDate(o.date, 'EEEE h:mm a', this.locale),
                series: series,
                name: o.name || 'Untitled',
                fullName: `${series ? series + ' - ' : ''}${o.name || 'Untitled'}`,
                speakers: (o.speakerIds || []).map(o => this.speakerById[o].name),
                status: o.status,
                statusString: Utility.StatusToText(o.status),
                statusColor: '#42C75D',
            };
            result.search = `${result.dateString}${result.timeString}${result.fullName}${result.speakers.join()}${result.statusString}`.toLowerCase();
            return result;
        });

        this.sort = 'id';
        this.ascending = false;
        this.updateEntries();
    }

    public onLoadMoreRows() {
        this.loaded = this.loaded.concat(this.filtered.slice(this.loaded.length, this.loaded.length + 30));
    }

    public onSort(sort: string) {
        if (sort !== this.sort) {
            this.sort = sort;
            this.ascending = true;
        } else if (!this.ascending) {
            this.sort = 'id';
            this.ascending = false;
        } else {
            this.ascending = false;
        }

        this.updateEntries();
    }

    private readonly SortOptions = {
        id: (a: MediaListEntry, b: MediaListEntry) => {
            return a.id - b.id;
        },
        date: (a: MediaListEntry, b: MediaListEntry) => {
            const A = a.date; const B = b.date;
            return A < B ? -1 : (A > B ? 1 : (a.id - b.id));
        },
        name: (a: MediaListEntry, b: MediaListEntry) => {
            const A = (a.fullName || 'zzz').toLowerCase().trim();
            const B = (b.fullName || 'zzz').toLowerCase().trim();
            return A < B ? -1 : (A > B ? 1 : (a.id - b.id));
        },
        speakers: (a: MediaListEntry, b: MediaListEntry) => {
            const A = a.speakers.length ? a.speakers[0].toLowerCase().trim() : 'zzz';
            const B = b.speakers.length ? b.speakers[0].toLowerCase().trim() : 'zzz';
            return A < B ? -1 : (A > B ? 1 : (a.id - b.id));
        },
        status: (a: MediaListEntry, b: MediaListEntry) => {
            const A = a.status; const B = b.status;
            return A < B ? -1 : (A > B ? 1 : (a.id - b.id));
        },
    };

    private updateEntries() {
        const search = (this.search || '').toLowerCase();
        const comparator = this.SortOptions[this.sort];
        this.filtered = this.entries.filter(o => !search || o.search.indexOf(search) >= 0).sort(this.ascending ? comparator : (a, b) => -comparator(a, b));
        this.loaded = this.filtered.slice(0, 30);
    }

    public onSearchUpdate() {
        this.updateEntries();
    }
}
