import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { AppService } from '@services/app/app.service';
import { App } from '@services/app/app.api';
import { ActivatedRoute } from '@angular/router';
import { finalize, map, mergeMap, tap } from 'rxjs/operators';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { SelectionBoxItem } from '@common/selection-box/selection-box.component';
import { SeriesManager } from '@services/app/managers/series.manager';
import { SpeakerManager } from '@services/app/managers/speaker.manager';
import { FeedManager } from '@services/app/managers/feed.manager';
import { TopicManager } from '@services/app/managers/topic.manager';
import { NotificationsService } from 'angular2-notifications';
import { forkJoin, of, Observable } from 'rxjs';
import { OrganizationManager } from '@services/app/managers/organization.manager';

@Component({
    selector: 'app-media-form',
    templateUrl: './media-form.component.html',
    styleUrls: ['./media-form.component.scss']
})
export class MediaFormComponent implements OnInit {
    @Input()
    public media: App.Media;

    @Output()
    public mediaChange = new EventEmitter<App.Media>();

    public hash: string;
    public series: App.Series;

    public form: FormGroup;
    public seriesItems: Array<SelectionBoxItem> = [];
    public speakerItems: Array<SelectionBoxItem> = [];
    public includeItems: Array<SelectionBoxItem> = [];
    public topicItems: Array<SelectionBoxItem> = [];

    public imageUrl: string;
    public seriesImageUrl: string;
    public organizationImageUrl: string;

    constructor(private app: AppService, private route: ActivatedRoute, private notifications: NotificationsService) { }

    public ngOnInit() {
        this.form = new FormGroup({});
        this.form.addControl('name', new FormControl(undefined, [Validators.required]));
        this.form.addControl('date', new FormControl(undefined));
        this.form.addControl('seriesIds', new FormControl(undefined));
        this.form.addControl('speakerIds', new FormControl(undefined));
        this.form.addControl('includeIds', new FormControl(undefined));
        this.form.addControl('topicIds', new FormControl(undefined));
        this.form.addControl('description', new FormControl(undefined));

        let series: Array<App.Series> = SeriesManager.sharedInstance.series;
        let speakers: Array<App.Speaker> = SpeakerManager.sharedInstance.speakers;
        let topics: Array<App.Topic> = TopicManager.sharedInstance.topics;

        this.seriesItems = series.sort((a, b) => {
            let A = (a.name || 'Untitled').trim().toLowerCase();
            let B = (b.name || 'Untitled').trim().toLowerCase();
            return (A < B ? -1 : (A > B ? 1 : 0));
        }).map(o => {
            return {
                display: o.name || 'Untitled',
                value: o.id,
                data: o,
            };
        });

        this.speakerItems = speakers.sort((a, b) => {
            let A = (a.name || 'Untitled').trim().toLowerCase();
            let B = (b.name || 'Untitled').trim().toLowerCase();
            return (A < B ? -1 : (A > B ? 1 : 0));
        }).map(o => {
            return {
                display: o.name || 'Untitled',
                value: o.id,
                data: o,
            };
        });

        let topicsByCategory: { [key: string]: Array<App.Topic> } = {};
        topics.forEach(o => {
            if (!topicsByCategory[o.category]) { topicsByCategory[o.category] = []; }
            topicsByCategory[o.category].push(o);
        });

        this.topicItems = (Object.keys(topicsByCategory).sort((a, b) => {
            let A = a.trim().toLowerCase();
            let B = b.trim().toLowerCase();
            return (A < B ? -1 : (A > B ? 1 : 0));
        }).map(key => {
            return topicsByCategory[key].sort((a, b) => {
                let A = (a.name || '').trim().toLowerCase();
                let B = (b.name || '').trim().toLowerCase();
                return (A < B ? -1 : (A > B ? 1 : 0));
            }).map((o, index) => {
                return {
                    display: o.name,
                    header: o.category,
                    value: o.id,
                    data: o,
                };
            });
        }) as any).flat();

        let feeds: Array<App.Feed> = FeedManager.sharedInstance.feeds.concat();
        feeds = feeds.map(o => { return { ...o, title: `${o.title || 'Untitled'} - ${o.type === 1 ? 'Audio' : 'Video'} Podcast` }; });
        feeds.push({ id: -1, title: 'HopeStream Mobile App' } as any);
        feeds.push({ id: -2, title: 'YouTube' } as any);

        this.includeItems = feeds.sort((a, b) => {
            let A = a.title.trim().toLowerCase();
            let B = b.title.trim().toLowerCase();
            return (A < B ? -1 : (A > B ? 1 : 0));
        }).map(o => {
            return {
                display: o.title,
                value: o.id,
                data: o,
            };
        });

        this.updateLastKnownIncludeIds();

        this.hash = this.app.hashids.encode(this.media.id);
        this.series = this.media.seriesId ? this.seriesItems.find(o => o.value === this.media.seriesId).data : undefined;

        this.form.controls.name.setValue(this.media.name);
        this.form.controls.date.setValue(this.media.date);
        this.form.controls.seriesIds.setValue(this.media.seriesId ? [this.media.seriesId] : undefined);
        this.form.controls.speakerIds.setValue(this.media.speakerIds);
        this.form.controls.includeIds.setValue(this.lastKnownIncludeIds.concat());
        this.form.controls.topicIds.setValue(this.media.topicIds);
        this.form.controls.description.setValue(this.media.description);

        const organization = OrganizationManager.sharedInstance.organization;
        this.imageUrl = this.media.imageUrl ? `${this.media.imageUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
        this.seriesImageUrl = this.series?.thumbnailUrl ? `${this.series.thumbnailUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
        this.organizationImageUrl = organization.thumbnailUrl ? `${organization.thumbnailUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
    }

    private lastKnownIncludeIds: Array<number> = [];
    private updateLastKnownIncludeIds() {
        let lastKnownIncludeIds: Array<number> = FeedManager.sharedInstance.feeds.filter(o => o.mediaIds.indexOf(this.media.id) >= 0).map(o => o.id);
        if (!this.media.hidden) { lastKnownIncludeIds.unshift(-1); }
        if (this.media.youtubeStatus) { lastKnownIncludeIds.unshift(-2); }
        this.lastKnownIncludeIds = lastKnownIncludeIds;
    }

    public saving: boolean = false;
    public onSave() {
        let series = (this.form.controls.seriesIds.value || []);
        let speakers = (this.form.controls.speakerIds.value || []);
        let topics = (this.form.controls.topicIds.value || []);
        let includes = (this.form.controls.includeIds.value || []);

        // update media
        let media: App.MediaUpdate = { id: this.media.id };
        media.name = this.form.controls.name.value;
        media.date = this.form.controls.date.value;
        media.description = this.form.controls.description.value || null;
        media.seriesId = series.length ? series[0] : null;
        media.speakerIds = speakers.length ? speakers : null;
        media.topicIds = topics.length ? topics : null;
        media.hidden = (includes.indexOf(-1) < 0);

        // very carefully determine if the youtube status needs to be updated
        const willIncludeInYouTube = !!includes.filter(o => o === -2).length;
        const didIncludeInYouTube = !!this.lastKnownIncludeIds.filter(o => o === -2).length;
        if (willIncludeInYouTube !== didIncludeInYouTube) {
            media.youtubeStatus = willIncludeInYouTube ? (this.media.youtubeId ? 'complete' : 'queued') : null;
            console.log("YouTube Status Changed: ", willIncludeInYouTube ? (this.media.youtubeId ? 'complete' : 'queued') : null);
        }
        if (willIncludeInYouTube) { media.youtubeMetadataUpdate = true; }

        // update feeds
        let before = this.lastKnownIncludeIds.filter(o => o >= 0);
        let after = includes.filter(o => o >= 0);
        let remove = before.filter(o => after.indexOf(o) < 0);
        let add = after.filter(o => before.indexOf(o) < 0);

        let updates: Array<Observable<void>> = remove.map(o => {
            return this.app.API.removeMediaIdFromFeed({ id: o } as App.Feed, this.media.id);
        }).concat(add.map(o => {
            return this.app.API.addMediaIdToFeed({ id: o } as App.Feed, this.media.id);
        }));
        if (!updates.length) { updates = [of(1).pipe(map(() => { }))]; }

        this.saving = true;
        forkJoin(this.app.API.updateMedia(media), updates).pipe(finalize(() => {
            this.saving = false;
        })).subscribe(responses => {
            remove.forEach(o => {
                let feed = FeedManager.sharedInstance.feeds.find(feed => feed.id === o);
                feed.mediaIds = feed.mediaIds.filter(o => o !== this.media.id);
            });
            add.forEach(o => {
                let feed = FeedManager.sharedInstance.feeds.find(feed => feed.id === o);
                feed.mediaIds.push(this.media.id);
            });

            this.media = responses[0];
            this.series = this.media.seriesId ? this.seriesItems.find(o => o.value === this.media.seriesId).data : undefined;
            this.seriesImageUrl = this.series?.thumbnailUrl ? `${this.series.thumbnailUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
            this.updateLastKnownIncludeIds();
            this.mediaChange.emit(this.media);

            this.notifications.success('Success', 'Media details saved successfully.', { timeOut: 5000 });
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem saving.');
        });
    }

    public uploading: File;
    public progress: number;

    public onImageSelected(event: any) {
        let file: File = event.target?.files?.length && event.target.files[0] || undefined;
        if (!file) { return; }

        this.uploading = file;
        this.progress = 0;

        this.app.API.uploadImageForMedia(this.media, file, progress => {
            this.progress = progress;
        }).pipe(finalize(() => {
            this.uploading = undefined;
            this.progress = 1;
        })).subscribe(media => {
            this.notifications.success('Success', 'Media image updated.', { timeOut: 5000 });
            this.media = media;
            this.imageUrl = this.media.imageUrl ? `${this.media.imageUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem uploading.');
        });
    }

    public selectingSeries: boolean = false;

    public onSeriesImageSelected() {
        this.selectingSeries = true;
        this.app.API.setMediaImageToSeries(this.media, this.series).pipe(finalize(() => {
            this.selectingSeries = false;
        })).subscribe(media => {
            this.notifications.success('Success', 'Media image updated.', { timeOut: 5000 });
            this.media = media;
            this.imageUrl = this.media.imageUrl ? `${this.media.imageUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem updating.');
        });
    }

    public selectingOrganization: boolean = false;

    public onOrganizationImageSelected() {
        this.selectingOrganization = true;
        this.app.API.setMediaImageToOrganization(this.media, OrganizationManager.sharedInstance.organization).pipe(finalize(() => {
            this.selectingOrganization = false;
        })).subscribe(media => {
            this.notifications.success('Success', 'Media image updated.', { timeOut: 5000 });
            this.media = media;
            this.imageUrl = this.media.imageUrl ? `${this.media.imageUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem updating.');
        });
    }
}
