import { Injectable } from '@angular/core';

@Injectable()
export class LayoutService {
    public sidebarOpen: boolean = true;
    public toolbarOpen: boolean = false;
    public toolbarWidth: number = this.storedToolbarWidth;
    public shortcutsVisible: boolean = false;

    public persistToolbarWidth() {
        this.storedToolbarWidth = this.toolbarWidth;
    }

    private readonly TOOLBAR_WIDTH_KEY = 'vyasa.toolbar.width';

    private get storedToolbarWidth(): number {
        let value = localStorage.getItem(this.TOOLBAR_WIDTH_KEY);
        if (!value) { return 480; }
        return parseInt(value);
    }

    private set storedToolbarWidth(value: number) {
        if (!value) {
            localStorage.removeItem(this.TOOLBAR_WIDTH_KEY)
        } else {
            localStorage.setItem(this.TOOLBAR_WIDTH_KEY, `${value}`);
        }
    }
}
