import { Observable } from 'rxjs';
import { Manager } from '@classes/manager';
import { App, AppAPI } from '../app.api';

export class SeriesManager extends Manager {
    public static sharedInstance: SeriesManager = undefined;

    public series: Array<App.Series> = [];

    constructor(private api: AppAPI) {
        super();
        SeriesManager.sharedInstance = this;
    }

    protected fetchData(): Observable<any> {
        return this.api.getAllSeries();
    }

    protected setData(data: any) {
        this.series = data;
    }

    public reset() {
        super.reset();
        this.series = [];
    }
}
