import { Component, LOCALE_ID, Inject } from "@angular/core";
import { AppService } from '@services/app/app.service';
import { App } from '@services/app/app.api';
import { Utility } from '@classes/utility';
import { finalize } from 'rxjs/operators';
import { Router } from '@angular/router';
import { NotificationsService } from 'angular2-notifications';

interface SeriesListEntry {
    id: number;
    name: string;
    thumbnailUrl?: string;
    search?: string;
}

@Component({
    selector: 'app-series-list',
    templateUrl: './series-list.component.html',
    styleUrls: ['./series-list.component.scss']
})
export class SeriesListComponent {
    public Utility = Utility;

    public series: Array<App.Series>;

    public entries: Array<SeriesListEntry> = [];
    public filtered: Array<SeriesListEntry> = [];

    public loaded: Array<SeriesListEntry> = [];
    public sort: string = 'id';
    public ascending: boolean = false;
    public search: string;

    constructor(
        @Inject(LOCALE_ID) public locale: string,
        private app: AppService,
        private router: Router,
        private notifications: NotificationsService) { }

    public ngOnInit() {
        this.app.API.getAllSeries().subscribe(series => {
            this.series = series;

            this.entries = this.series.map(o => {
                let result: SeriesListEntry = {
                    id: o.id,
                    name: o.name || 'Untitled',
                    thumbnailUrl: o.thumbnailUrl,
                };
                result.search = `${result.name}`.toLowerCase();
                return result;
            });

            this.sort = 'id';
            this.ascending = false;
            this.updateEntries();
        });
    }

    public onLoadMoreRows() {
        this.loaded = this.loaded.concat(this.filtered.slice(this.loaded.length, this.loaded.length + 30));
    }

    public onSort(sort: string) {
        if (sort !== this.sort) {
            this.sort = sort;
            this.ascending = true;
        } else if (!this.ascending) {
            this.sort = 'id';
            this.ascending = false;
        } else {
            this.ascending = false;
        }

        this.updateEntries();
    }

    private readonly SortOptions = {
        id: (a: SeriesListEntry, b: SeriesListEntry) => {
            return a.id - b.id;
        },
        name: (a: SeriesListEntry, b: SeriesListEntry) => {
            const A = (a.name || 'zzz').toLowerCase().trim();
            const B = (b.name || 'zzz').toLowerCase().trim();
            return A < B ? -1 : (A > B ? 1 : (a.id - b.id));
        },
    };

    private updateEntries() {
        const search = (this.search || '').toLowerCase();
        const comparator = this.SortOptions[this.sort];
        this.filtered = this.entries.filter(o => !search || o.search.indexOf(search) >= 0).sort(this.ascending ? comparator : (a, b) => -comparator(a, b));
        this.loaded = this.filtered.slice(0, 30);
    }

    public onSearchUpdate() {
        this.updateEntries();
    }

    public creating: boolean = false;
    public onAddSeries() {
        this.creating = true;
        this.app.API.createSeries().pipe(finalize(() => {
            this.creating = false;
        })).subscribe(series => {
            this.router.navigate(['/series', series.id]);
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem creating.');
        });
    }
}
