import { Component, LOCALE_ID, Inject } from "@angular/core";
import { AppService } from '@services/app/app.service';
import { App } from '@services/app/app.api';
import { Utility } from '@classes/utility';
import { finalize } from 'rxjs/operators';
import { Router } from '@angular/router';
import { NotificationsService } from 'angular2-notifications';

interface FeedListEntry {
    id: number;
    type: string;
    title: string;
    subtitle: string;
    search?: string;
}

@Component({
    selector: 'app-feed-list',
    templateUrl: './feed-list.component.html',
    styleUrls: ['./feed-list.component.scss']
})
export class FeedListComponent {
    public Utility = Utility;

    public feeds: Array<App.Feed>;

    public entries: Array<FeedListEntry> = [];
    public filtered: Array<FeedListEntry> = [];

    public loaded: Array<FeedListEntry> = [];
    public sort: string = 'id';
    public ascending: boolean = false;
    public search: string;

    constructor(
        @Inject(LOCALE_ID) public locale: string,
        private app: AppService,
        private router: Router,
        private notifications: NotificationsService) { }

    public ngOnInit() {
        this.app.API.getAllFeeds().subscribe(feeds => {
            this.feeds = feeds;

            this.entries = this.feeds.map(o => {
                let result: FeedListEntry = {
                    id: o.id,
                    type: o.type ? 'Audio Podcast' : 'Video Podcast',
                    title: o.title || 'Untitled',
                    subtitle: o.subtitle || '',
                };
                result.search = `${result.type}${result.title}${result.subtitle}`.toLowerCase();
                return result;
            });

            this.sort = 'id';
            this.ascending = false;
            this.updateEntries();
        });
    }

    public onLoadMoreRows() {
        this.loaded = this.loaded.concat(this.filtered.slice(this.loaded.length, this.loaded.length + 30));
    }

    public onSort(sort: string) {
        if (sort !== this.sort) {
            this.sort = sort;
            this.ascending = true;
        } else if (!this.ascending) {
            this.sort = 'id';
            this.ascending = false;
        } else {
            this.ascending = false;
        }

        this.updateEntries();
    }

    private readonly SortOptions = {
        id: (a: FeedListEntry, b: FeedListEntry) => {
            return a.id - b.id;
        },
        type: (a: FeedListEntry, b: FeedListEntry) => {
            const A = (a.type || 'zzz').toLowerCase().trim();
            const B = (b.type || 'zzz').toLowerCase().trim();
            return A < B ? -1 : (A > B ? 1 : (a.id - b.id));
        },
        title: (a: FeedListEntry, b: FeedListEntry) => {
            const A = (a.title || 'zzz').toLowerCase().trim();
            const B = (b.title || 'zzz').toLowerCase().trim();
            return A < B ? -1 : (A > B ? 1 : (a.id - b.id));
        },
        subtitle: (a: FeedListEntry, b: FeedListEntry) => {
            const A = (a.subtitle || 'zzz').toLowerCase().trim();
            const B = (b.subtitle || 'zzz').toLowerCase().trim();
            return A < B ? -1 : (A > B ? 1 : (a.id - b.id));
        },
    };

    private updateEntries() {
        const search = (this.search || '').toLowerCase();
        const comparator = this.SortOptions[this.sort];
        this.filtered = this.entries.filter(o => !search || o.search.indexOf(search) >= 0).sort(this.ascending ? comparator : (a, b) => -comparator(a, b));
        this.loaded = this.filtered.slice(0, 30);
    }

    public onSearchUpdate() {
        this.updateEntries();
    }

    public creating: boolean = false;
    public onAddFeed() {
        this.creating = true;
        this.app.API.createFeed().pipe(finalize(() => {
            this.creating = false;
        })).subscribe(feed => {
            this.router.navigate(['/feeds', feed.id]);
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem creating.');
        });
    }
}
