import { AppAPI } from './app.api';
import { AuthService } from '../auth.service';
import { Injectable } from '@angular/core';
import { forkJoin, Observable } from 'rxjs';
import { Manager } from '@classes/manager';
import { Router } from '@angular/router';
import { UserManager } from './managers/user.manager';
import { SeriesManager } from './managers/series.manager';
import { SpeakerManager } from './managers/speaker.manager';
import { FeedManager } from './managers/feed.manager';
import { TopicManager } from './managers/topic.manager';
import { OrganizationManager } from './managers/organization.manager';
import Hashids from 'hashids';

@Injectable()
export class AppService {
    public API: AppAPI;
    public hashids = new Hashids('ca8b97bb-de3d-4d22-8716-195354fa1e4c', 11);

    public userManager: UserManager;
    public organizationManager: OrganizationManager;
    public seriesManager: SeriesManager;
    public speakerManager: SpeakerManager;
    public feedManager: FeedManager;
    public topicManager: TopicManager;

    private allManagers: Array<Manager> = [];

    constructor(
        public auth: AuthService,
        private router: Router,
    ) {
        this.API = new AppAPI(auth);

        this.userManager = new UserManager(this.API);
        this.organizationManager = new OrganizationManager(this.API);
        this.seriesManager = new SeriesManager(this.API);
        this.speakerManager = new SpeakerManager(this.API);
        this.feedManager = new FeedManager(this.API);
        this.topicManager = new TopicManager(this.API);

        this.allManagers = [
            this.userManager,
            this.organizationManager,
            this.seriesManager,
            this.speakerManager,
            this.feedManager,
            this.topicManager];
    }

    public start(): Observable<any> {
        this.allManagers.forEach(o => o.enabled = true);
        return forkJoin(...this.allManagers.map(o => o.refresh()));
    }

    public stop() {
        this.allManagers.forEach(manager => {
            manager.enabled = false;
            manager.cancel();
            manager.reset();
        });
    }
}
