import { Component } from "@angular/core";
import { finalize } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { FormGroup } from '@angular/forms';
import { NotificationsService } from 'angular2-notifications';
import { StateService } from '@services/state.service';
import { Validators } from '@angular/forms';
import { AuthService } from "@services/auth.service";

@Component({
    selector: 'app-sign-in',
    templateUrl: './sign-in.component.html',
    styleUrls: ['./sign-in.component.scss']
})
export class SignInComponent {
    public visible: number = 0;
    public form: FormGroup;
    public loading: boolean = false;

    constructor(private state: StateService, public auth: AuthService, private notifications: NotificationsService) {
        this.form = new FormGroup({});
        this.form.addControl('name', new FormControl('', [Validators.required]));
        this.form.addControl('email', new FormControl('', [Validators.required]));
        this.form.addControl('password', new FormControl('', [Validators.required, Validators.minLength(8)]));
    }

    public onSignIn() {
        this.loading = true;
        this.state.login(this.form.controls['email'].value.trim(), this.form.controls['password'].value).pipe(finalize(() => {
            this.loading = false;
        })).subscribe(user => { }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem logging in');
        });
    }

    public onSignUp() {

    }

    public onResetPassword() {
        this.loading = true;
        this.auth.userForgotPassword(this.form.controls.email.value.trim()).pipe(finalize(() => {
            this.loading = false;
        })).subscribe(() => {
            this.visible = 0;
            this.notifications.success('Success', 'You will receive an e-mail with instructions to reset your password.', { timeOut: 5000 });
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem logging in');
        });
    }
}
