import { Observable, forkJoin, of } from 'rxjs';
import { Manager } from '@classes/manager';
import { App, AppAPI } from '../app.api';
import { mergeMap, map } from 'rxjs/operators';

export class FeedManager extends Manager {
    public static sharedInstance: FeedManager = undefined;

    public feeds: Array<App.Feed> = [];

    constructor(private api: AppAPI) {
        super();
        FeedManager.sharedInstance = this;
    }

    protected fetchData(): Observable<any> {
        return this.api.getAllFeeds().pipe(mergeMap(feeds => {
            if (!feeds.length) { return of([]); }
            return forkJoin(feeds.map(feed => {
                return this.api.getMediaIdsForFeed(feed).pipe(map(ids => {
                    feed.mediaIds = ids;
                    return feed;
                }));
            }));
        }));
    }

    protected setData(data: any) {
        this.feeds = data;
    }

    public reset() {
        super.reset();
        this.feeds = [];
    }
}
