import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BaseComponent } from './base/base.component';
import { LoadingGuard } from '@services/loading-guard';
import { AuthGuard } from '@services/auth-guard';
import { SignInComponent } from './sign-in/sign-in/sign-in.component';
import { MediaListComponent } from './media/media-list/media-list.component';
import { MediaDetailComponent } from './media/media-detail/media-detail.component';
import { OrganizationDetailComponent } from './organization/organization-detail/organization-detail.component';
import { SeriesListComponent } from './series/series-list/series-list.component';
import { SeriesDetailComponent } from './series/series-detail/series-detail.component';
import { SpeakerListComponent } from './speaker/speaker-list/speaker-list.component';
import { SpeakerDetailComponent } from './speaker/speaker-detail/speaker-detail.component';
import { FeedListComponent } from './feed/feed-list/feed-list.component';
import { FeedDetailComponent } from './feed/feed-detail/feed-detail.component';
import { MediaUploadComponent } from './media/media-upload/media-upload.component';
import { RedirectComponent } from '@common/redirect/redirect.component';
import { SettingsComponent } from './settings/settings.component';

const routes: Routes = [
    {
        path: '', component: BaseComponent, canActivate: [LoadingGuard, AuthGuard], canActivateChild: [AuthGuard], children: [
            { path: 'church', component: OrganizationDetailComponent },
            { path: '', component: RedirectComponent },
            { path: 'media', component: MediaListComponent },
            { path: 'media/:id', component: MediaDetailComponent },
            { path: 'upload', component: MediaUploadComponent },
            { path: 'upload/:id', component: MediaUploadComponent },
            { path: 'series', component: SeriesListComponent },
            { path: 'series/:id', component: SeriesDetailComponent },
            { path: 'speakers', component: SpeakerListComponent },
            { path: 'speakers/:id', component: SpeakerDetailComponent },
            { path: 'feeds', component: FeedListComponent },
            { path: 'feeds/:id', component: FeedDetailComponent },
            { path: 'settings', component: SettingsComponent },
        ]
    },
    { path: 'sign-in', canActivate: [AuthGuard], component: SignInComponent },
    { path: '**', redirectTo: 'sign-in' },
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { useHash: true })],
    exports: [RouterModule]
})
export class RoutingModule { }
