import { Observable } from 'rxjs';
import { Manager } from '@classes/manager';
import { App, AppAPI } from '../app.api';

export class OrganizationManager extends Manager {
    public static sharedInstance: OrganizationManager = undefined;

    public organization: App.Organization;

    constructor(private api: AppAPI) {
        super();
        OrganizationManager.sharedInstance = this;
    }

    protected fetchData(): Observable<any> {
        return this.api.getCurrentOrganization();
    }

    protected setData(data: any) {
        this.organization = data;
    }

    public reset() {
        super.reset();
        this.organization = undefined;
    }
}
