import { Component, OnInit } from "@angular/core";
import { AppService } from '@services/app/app.service';
import { App } from '@services/app/app.api';
import { ActivatedRoute, Router } from '@angular/router';
import { finalize, mergeMap, tap } from 'rxjs/operators';
import { Utility } from '@classes/utility';
import { FormGroup, FormControl } from '@angular/forms';
import { NotificationsService } from 'angular2-notifications';
import { forkJoin, of } from 'rxjs';

@Component({
    selector: 'app-series-detail',
    templateUrl: './series-detail.component.html',
    styleUrls: ['./series-detail.component.scss']
})
export class SeriesDetailComponent implements OnInit {
    public Utility = Utility;

    public loading = true;
    public series: App.Series;
    public media: Array<App.Media> = [];
    public form: FormGroup;
    public imageUrl: string;

    constructor(
        private app: AppService,
        private route: ActivatedRoute,
        private router: Router,
        private notifications: NotificationsService) { }

    public ngOnInit() {
        this.form = new FormGroup({});
        this.form.addControl('name', new FormControl(undefined));
        this.form.addControl('description', new FormControl(undefined));

        let id = parseInt(this.route.snapshot.params.id);
        forkJoin(this.app.API.getSeries(id), this.app.API.getAllMedia()).pipe(finalize(() => {
            this.loading = false;
        })).subscribe(responses => {
            this.series = responses[0];
            this.media = responses[1].filter(o => o.seriesId === this.series.id);
            this.form.controls.name.setValue(this.series.name);
            this.form.controls.description.setValue(this.series.description);
            this.imageUrl = this.series.imageUrl ? `${this.series.imageUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
        });
    }

    public saving: boolean = false;
    public onSave() {
        let series: App.SeriesUpdate = { id: this.series.id };
        series.name = this.form.controls.name.value;
        series.description = this.form.controls.description.value || null;

        this.saving = true;
        this.app.API.updateSeries(series).pipe(finalize(() => {
            this.saving = false;
        })).subscribe(series => {
            this.series = series;
            this.notifications.success('Success', 'Series details saved successfully.', { timeOut: 5000 });
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem saving.');
        });
    }

    public uploading: File;
    public progress: number;

    public onImageSelected(event: any) {
        let file: File = event.target?.files?.length && event.target.files[0] || undefined;
        if (!file) { return; }

        this.uploading = file;
        this.progress = 0;

        this.app.API.uploadImageForSeries(this.series, file, progress => {
            this.progress = progress;
        }).pipe(finalize(() => {
            this.uploading = undefined;
            this.progress = 1;
        })).subscribe(series => {
            this.notifications.success('Success', 'Series image updated.', { timeOut: 5000 });
            this.series = series;
            this.imageUrl = this.series.imageUrl ? `${this.series.imageUrl.replace('static.hopestream.com', 'hopestream.s3.amazonaws.com')}?t=${new Date().valueOf()}` : undefined;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem uploading.');
        });
    }

    public deleteDialogVisible: boolean = false;
    public deleting: boolean = false;

    public onDelete() {
        this.deleting = true;
        this.app.API.deleteSeries(this.series).pipe(finalize(() => {
            this.deleting = false;
        })).subscribe(() => {
            this.router.navigate(['/series']);
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem deleting.');
        });
    }
}
