import { Component, OnInit } from "@angular/core";
import { AppService } from '@services/app/app.service';
import { App } from '@services/app/app.api';
import { ActivatedRoute, Router } from '@angular/router';
import { finalize, map, mergeMap, tap, catchError } from 'rxjs/operators';
import { Utility } from '@classes/utility';
import { FormGroup, FormControl } from '@angular/forms';
import { SelectionBoxItem } from '@common/selection-box/selection-box.component';
import { SeriesManager } from '@services/app/managers/series.manager';
import { SpeakerManager } from '@services/app/managers/speaker.manager';
import { FeedManager } from '@services/app/managers/feed.manager';
import { TopicManager } from '@services/app/managers/topic.manager';
import { NotificationsService } from 'angular2-notifications';
import { forkJoin, of, Observable } from 'rxjs';
import { OrganizationManager } from '@services/app/managers/organization.manager';
import { environment } from '@environments/environment';
import { HttpClient } from '@angular/common/http';

@Component({
    selector: 'app-media-detail',
    templateUrl: './media-detail.component.html',
    styleUrls: ['./media-detail.component.scss']
})
export class MediaDetailComponent implements OnInit {
    public Utility = Utility;

    public loading = true;
    public media: App.Media;
    public series: App.Series;

    public hash: string;
    public videoPlayerUrl: string;
    public videoStreamUrl: string;
    public videoFileDefaultUrl: string;
    public videoFileSourceUrl: string;
    public audioFileUrl: string;
    public captionUrl: string;

    constructor(
        private app: AppService,
        private route: ActivatedRoute,
        private router: Router,
        private notifications: NotificationsService,
        private http: HttpClient) { }

    public ngOnInit() {
        const id = parseInt(this.route.snapshot.params.id);
        const hash = this.app.hashids.encode(id);
        const captionUrl = `https://hopestream.s3.amazonaws.com/media/${hash}/captions-en.vtt?t=${new Date().valueOf()}`;

        forkJoin(
            this.app.API.getMedia(id),
            this.http.get(captionUrl, { responseType: 'blob' }).pipe(catchError(() => of(undefined)))
        ).pipe(finalize(() => {
            this.loading = false;
        })).subscribe(responses => {
            this.media = responses[0];
            this.series = this.media.seriesId ? SeriesManager.sharedInstance.series.find(o => o.id === this.media.seriesId) : undefined;

            this.hash = hash;
            this.videoPlayerUrl = `${environment.playerUrl}?id=${this.hash}`;
            this.videoStreamUrl = `${environment.staticUrl}media/${this.hash}/master.m3u8`;
            this.videoFileDefaultUrl = `${environment.staticUrl}media/${this.hash}/video-default.mp4`;
            this.videoFileSourceUrl = `${environment.staticUrl}media/${this.hash}/video-source.mp4`;
            this.audioFileUrl = `${environment.staticUrl}media/${this.hash}/audio.mp3`;
            this.captionUrl = responses[1] ? `${environment.staticUrl}media/${this.hash}/captions-en.vtt` : undefined;
        });
    }

    public onMediaChange(media: App.Media) {
        this.media = media;
        this.series = media.seriesId ? SeriesManager.sharedInstance.series.find(o => o.id === media.seriesId) : undefined;
    }

    public onReplaceMedia() {
        this.router.navigate(['/upload', this.media.id]);
    }

    public deleteDialogVisible: boolean = false;
    public deleting: boolean = false;

    public onDelete() {
        this.deleting = true;
        this.app.API.deleteMedia(this.media).pipe(finalize(() => {
            this.deleting = false;
        })).subscribe(() => {
            this.router.navigate(['/media']);
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem deleting.');
        });
    }

    public uploading: File;
    public progress: number;

    public onCaptionFileSelected(event: any) {
        let file: File = event.target?.files?.length && event.target.files[0] || undefined;
        if (!file) { return; }

        this.uploading = file;
        this.progress = 0;

        this.app.API.uploadCaptionFileForMedia(this.media, file, progress => {
            this.progress = progress;
        }).pipe(finalize(() => {
            this.uploading = undefined;
            this.progress = 1;
        })).subscribe(() => {
            this.notifications.success('Success', 'Caption file uploaded.', { timeOut: 5000 });
            this.captionUrl = `${environment.staticUrl}media/${this.hash}/captions-en.vtt`;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem uploading.');
        });
    }
}
