import { Component } from "@angular/core";
import { Router } from '@angular/router';
import { StateService } from '@services/state.service';
import { UserManager } from '@services/app/managers/user.manager';
import { App } from '@services/app/app.api';
import { OrganizationManager } from '@services/app/managers/organization.manager';
import { HideAllPopovers } from '@common/popover/popover.component';

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})
export class HeaderComponent {
    public get user(): App.User { return UserManager.sharedInstance.user; }
    public get organization(): App.Organization { return OrganizationManager.sharedInstance.organization; }

    constructor(public router: Router, private state: StateService) { }

    public onViewSettings() {
        HideAllPopovers();
        this.router.navigate(['/settings']);
    }

    public onLogOut() {
        this.state.logout();
    }
}
