import { Observable } from 'rxjs';
import { Manager } from '@classes/manager';
import { App, AppAPI } from '../app.api';

export class TopicManager extends Manager {
    public static sharedInstance: TopicManager = undefined;

    public topics: Array<App.Topic> = [];

    constructor(private api: AppAPI) {
        super();
        TopicManager.sharedInstance = this;
    }

    protected fetchData(): Observable<any> {
        return this.api.getAllTopics();
    }

    protected setData(data: any) {
        this.topics = data;
    }

    public reset() {
        super.reset();
        this.topics = [];
    }
}
