import { Injectable } from "@angular/core";
import { environment } from '@environments/environment';
import { BehaviorSubject, Observable, of as ObservableOf, Subject, throwError } from "rxjs";
import { map, mergeMap, timeout } from "rxjs/operators";
import { HttpClient, HttpResponse } from '@angular/common/http';

const OAUTH_CLIENT_ID = 'hopestream-admin';
const OAUTH_CLIENT_SECRET = 'f085c37a-5d21-4eb4-8b73-355c11d2e60d';
const OAUTH_CLIENT_BASE_64 = btoa(`${OAUTH_CLIENT_ID}:${OAUTH_CLIENT_SECRET}`);

interface Credentials {
    accessToken: string;
    refreshToken?: string;
}

interface PendingRequest {
    method: string;
    path: string;
    params: { [key: string]: string | Array<string> };
    headers: { [key: string]: string };
    data?: any;
}

class PendingSubject extends Subject<HttpResponse<any>> {
    constructor(public request: PendingRequest) { super(); }
}

@Injectable()
export class AuthService {
    public loggedIn$: BehaviorSubject<boolean> = new BehaviorSubject(false);
    public get accessToken(): string { return this.credentials && this.credentials.accessToken; }

    constructor(private http: HttpClient) {
        this.loggedIn$.next(!!this.credentials);
    }

    private readonly CREDENTIALS_KEY = 'hopestream.credentials';
    private get credentials(): Credentials {
        let credentials = localStorage.getItem(this.CREDENTIALS_KEY);
        if (!credentials) { return undefined; }

        let result = JSON.parse(credentials) as Credentials;

        return result;
    }
    private set credentials(credentials: Credentials) {
        if (!credentials) {
            localStorage.removeItem(this.CREDENTIALS_KEY)
        } else {
            localStorage.setItem(this.CREDENTIALS_KEY, JSON.stringify(credentials));
        }
    }

    public userLogin(username: string, password: string): Observable<boolean> {
        return this.oauthToken({
            'grant_type': 'password',
            'scope': 'admin',
            'password': password,
            'username': username,
        }).pipe(map(credentials => {
            this.credentials = credentials;
            this.loggedIn$.next(true);
            return true;
        }));
    }

    public userLogout(): Observable<boolean> {
        this.credentials = undefined;
        this.loggedIn$.next(false);

        return ObservableOf(true);
    }

    public userRefresh(token: string): Observable<boolean> {
        return this.oauthToken({ 'grant_type': 'refresh_token', 'refresh_token': token })
            .pipe(map(credentials => {
                this.credentials = credentials;
                this.loggedIn$.next(true);
                return true;
            }));
    }

    public userRegister(name: string, email: string, password: string): Observable<void> {
        return this.getClientCredentialsHeaders().pipe(mergeMap(headers => {
            let data = {
                name: name,
                email: email.toLowerCase(),
                password: password
            };

            return this.POST('/account/register', {}, headers, data);
        }), map(response => { }));
    }

    public userForgotPassword(email: string): Observable<void> {
        return this.getClientCredentialsHeaders().pipe(mergeMap(headers => {
            let data = { email: email };

            return this.POST('/account/reset-password', {}, headers, data);
        }), map(() => { }));
    }

    private getClientCredentialsHeaders(): Observable<{ [key: string]: string }> {
        return this.oauthToken({ 'grant_type': 'client_credentials' })
            .pipe(map(credentials => {
                return { 'Authorization': 'Bearer ' + credentials.accessToken };
            }));
    }

    private oauthToken(params: { [key: string]: string }): Observable<Credentials> {
        let headers = {
            'Content-Type': 'application/x-www-form-urlencoded',
            'Authorization': 'Basic ' + OAUTH_CLIENT_BASE_64
        };

        let data = Object.keys(params).map(o => `${o}=${encodeURIComponent(params[o])}`).join('&');

        return this.POST('/oauth/token', {}, headers, data).pipe(map(response => {
            let data = response.body;
            if (!data.access_token) { throw 'Invalid access token'; }
            return { accessToken: data.access_token, refreshToken: data.refresh_token };
        }));
    }

    private request(method: string, path: string, params_: { [key: string]: string | Array<string> }, headers_: { [key: string]: string }, data: any): Observable<HttpResponse<any>> {
        let headers: { [key: string]: string | Array<string> } = { ...(headers_ || {}) };

        if (!!data && !headers['Content-Type']) { headers['Content-Type'] = 'application/json'; }
        if (!headers['Accept']) { headers['Accept'] = 'application/json'; }

        let responseType: 'blob' | 'json' | 'text' =
            headers['Accept'] === 'application/octet-stream' ? 'blob' : (headers['Accept'] === 'application/json' ? 'json' : 'text');

        let credentials = this.credentials;
        if (credentials && !headers['Authorization']) { headers['Authorization'] = `Bearer ${credentials.accessToken}`; }

        // Workaround https://github.com/angular/angular/issues/11058
        let params: { [key: string]: string | Array<string> } = { ...(params_ || {}) };
        for (let key in params) {
            if (params[key] === undefined) {
                delete params[key];
            } else {
                let array: Array<any> = Array.isArray(params[key]) ? params[key] as Array<string> : [params[key] as string];
                array = array.map(o => {
                    if (!((o as any) instanceof Date) || !o.getTime()) { return o; }
                    return o.toISOString(); // serialize dates in a specific way
                }).filter(o => o !== undefined);
                if (!array.length) {
                    delete params[key];
                } else {
                    params[key] = array.length === 1 ? array[0] : array;
                }
            }
        }

        let url = environment.apiUrl + path;

        let subject = new Subject<HttpResponse<any>>();
        this.http.request(method, url, {
            body: data,
            headers: headers,
            params: params,
            responseType: responseType,
            observe: 'response',
        }).subscribe(response => {
            subject.next(response);
            subject.complete();
        }, response => {
            this.fail(response, { 'method': method, 'path': path, 'params': params_, 'headers': headers_, 'data': data }).subscribe(subject);
        });

        return subject;
    }

    public GET(path: string, params: { [key: string]: string | Array<string> }, headers: { [key: string]: string } = {}, data: any = undefined): Observable<HttpResponse<any>> {
        return this.request('GET', path, params, headers, data);
    }

    public POST(path: string, params: { [key: string]: string | Array<string> }, headers: { [key: string]: string } = {}, data: any = undefined): Observable<HttpResponse<any>> {
        return this.request('POST', path, params, headers, data);
    }

    public PUT(path: string, params: { [key: string]: string | Array<string> }, headers: { [key: string]: string } = {}, data: any = undefined): Observable<HttpResponse<any>> {
        return this.request('PUT', path, params, headers, data);
    }

    public PATCH(path: string, params: { [key: string]: string | Array<string> }, headers: { [key: string]: string } = {}, data: any = undefined): Observable<HttpResponse<any>> {
        return this.request('PATCH', path, params, headers, data);
    }

    public DELETE(path: string, params: { [key: string]: string | Array<string> }, headers: { [key: string]: string } = {}, data: any = undefined): Observable<HttpResponse<any>> {
        return this.request('DELETE', path, params, headers, data);
    }

    private fail(response: HttpResponse<any>, request: PendingRequest): Observable<HttpResponse<any>> {
        return this.shouldAttemptRequestAgainAfterRefreshing(response, request) || throwError(response);
    }

    private pending: Array<PendingSubject> = undefined;
    private shouldAttemptRequestAgainAfterRefreshing(response: HttpResponse<any>, request: PendingRequest): Observable<HttpResponse<any>> {
        var result: Observable<HttpResponse<any>> = undefined;

        let json = undefined;
        try { json = response && ((response as any).error || response.body); } catch (error) { }
        json = json && json.error || json;
        let error = json && typeof json === 'string' && json.toLowerCase() || '';

        if ((error.indexOf('invalid_token') >= 0 || error.indexOf('unauthorized') >= 0)
            && (!request.params.grant_type || request.params.grant_type !== 'refresh_token')) {
            let subject = new PendingSubject(request);
            result = subject;

            if (!this.pending) {
                this.pending = new Array<PendingSubject>();
                this.pending.push(subject);
                this.processRefresh();
            } else {
                this.pending.push(subject);
            }
        }

        return result;
    }

    private processRefresh() {
        let credentials = this.credentials;
        if (!credentials || !credentials.accessToken) {
            this.userLogout();
            this.completeRefresh();
            return;
        }

        if (!!credentials.refreshToken) {
            this.userRefresh(credentials.refreshToken)
                .subscribe(response => {
                    this.completeRefresh();
                }, response => {
                    this.userLogout();
                    this.completeRefresh();
                });
        }
        else {
            this.userLogout();
            this.completeRefresh();
        }
    }

    private completeRefresh() {
        let pending = this.pending;
        this.pending = undefined;

        if (!pending) { return; }

        let credentials = this.credentials;
        for (let subject of pending) {
            if (credentials && credentials.accessToken) {
                let request = subject.request;
                this.request(request.method, request.path, request.params, request.headers, request.data).subscribe(subject);
            } else {
                console.error('completeRefresh() Invalid access token');
                throwError({
                    'statusText': 'Invalid access token.',
                    'status': 401
                }).subscribe(subject);
            }
        }
    }

    public UPLOAD(path: string, data: string | Blob, name: string, progress: (progress: number) => void = undefined): Observable<any> {
        let form: FormData = new FormData();
        form.append(name, data, name);

        let result = new Subject<any>();
        let xhr: XMLHttpRequest = new XMLHttpRequest();
        xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
                if (xhr.status === 200 || xhr.status === 204) {
                    result.next(xhr.response && JSON.parse(xhr.response) || {});
                    result.complete();
                } else {
                    result.error(xhr.response);
                }
            }
        };

        xhr.upload.onprogress = $event => {
            let value = ($event.loaded / $event.total);
            progress && progress(value);
        };

        xhr.open('POST', environment.apiUrl + path, true);
        xhr.setRequestHeader('Authorization', 'Bearer ' + this.accessToken);
        xhr.send(form);

        return result;
    }

    public stripAPIBaseURL(url: string): string {
        return url.replace(environment.apiUrl, '');
    }
}
