import { Observable } from 'rxjs';
import { Manager } from '@classes/manager';
import { App, AppAPI } from '../app.api';

const USER_KEY = 'hopestream.user';

export class UserManager extends Manager {
    public static sharedInstance: UserManager = undefined;

    private _user: App.User = undefined;
    public get user(): App.User { return this._user; }
    public set user(user: App.User) {
        if (!!user) { this.lastRefreshTime = Date.now(); }
        this._user = user;
        this.writeUserToLocalStorage();
        this.update$.next();
    }

    constructor(private api: AppAPI) {
        super();
        UserManager.sharedInstance = this;
        this._user = this.readUserFromLocalStorage();
    }

    protected fetchData(): Observable<any> {
        return this.api.getCurrentUser();
    }

    protected setData(data: any) {
        this._user = data;
        this.writeUserToLocalStorage();
    }

    public reset() {
        super.reset();
        this.user = undefined;
    }

    private readUserFromLocalStorage() {
        return JSON.parse(localStorage.getItem(USER_KEY));
    }

    private writeUserToLocalStorage() {
        if (!!this.user) {
            localStorage.setItem(USER_KEY, JSON.stringify(this.user));
        } else {
            localStorage.removeItem(USER_KEY);
        }
    }
}
