    public updateMediaStatus(media: App.Media, status: number): Observable<App.Media> {
        let data: any = { status: status };

        if (status === 1) {
            data.youtubeStatus = media.youtubeStatus ? 'queued' : null;
            data.vimeoStatus = null;
            data.audioStatus = null;
        }

        return this.auth.PUT(`/admin/api/1/media/${media.id}`, {}, {}, data).pipe(mergeMap(response => {
            return this.getMedia(media.id);
        }));
    }

