    public uploadCaptionFileForMedia(media: App.Media, captions: File, progress: (progress: number) => void = undefined): Observable<void> {
        return this.auth.UPLOAD(`/admin/api/1/media/${media.id}/captions`, captions, 'captions', progress).pipe(map(() => { }));
    };

    private getUploadPolicyForMedia(media: App.Media, file: File): Observable<App.MediaUploadPolicy> {
        return this.auth.GET(`/admin/api/1/media/${media.id}/upload-policy`, { contentType: file.type }).pipe(map(response => {
            return App.MediaUploadPolicy.fromJSON(response.body);
        }));
    }

    public uploadFileForMedia(media: App.Media, file: File, progress: (progress: number) => void = undefined): Observable<any> {
        return this.getUploadPolicyForMedia(media, file).pipe(mergeMap(policy => {
            let form: FormData = new FormData();
            form.append('key', policy.key);
            form.append('AWSAccessKeyId', policy.accessKeyId);
            form.append('acl', policy.acl);
            form.append('policy', policy.policy);
            form.append('signature', policy.signature);
            form.append('Content-Type', file.type);
            form.append('file', file, file.name);

            let result = new Subject<any>();
            let xhr: XMLHttpRequest = new XMLHttpRequest();
            xhr.onreadystatechange = () => {
                if (xhr.readyState === 4) {
                    if (xhr.status === 200 || xhr.status === 204) {
                        result.next(xhr.response && JSON.parse(xhr.response) || {});
                        result.complete();
                    } else {
                        result.error(xhr.response);
                    }
                }
            };

            xhr.upload.onprogress = $event => {
                let value = ($event.loaded / $event.total);
                progress && progress(value);
            };

            xhr.open('POST', policy.url, true);
            xhr.send(form);

            return result;
        }));
    }

    public deleteMedia(media: App.Media): Observable<void> {
        return this.auth.DELETE(`/admin/api/1/media/${media.id}`, {}).pipe(map(() => { }));
    }

